html body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

body {
  flex-flow: column;
  display: flex;
}

.vcenter {
  text-align: center;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#timeline, #around {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

#around > table {
  text-align: center;
  margin: auto;
